  .custom-file-upload {
    border: 1px solid #e4dddd;
    display: inline-block;
    padding: 3px 12px;
    cursor: pointer;
    background-color: #fefefe;
    border-radius: 4px;
    color: #333;
    margin-bottom: 16px;
  }
  
  .custom-file-upload:hover {
    background-color: #e0e0e0;
  }
  
  .notice-link {
    text-decoration: none;
    color: #2D3436;
  }